<template>
    <div class="ms-date-range-single-field" >
        <div class="ms-picker" v-show="calendarShow">
            <div v-el:date-picker class="ms-picker-box ms-picker-box-down" >
                <datepicker-range-single :start-day.sync="startDayObj"
                                         :end-day.sync="endDayObj"
                                         :selected-dates.sync="selectedDatesObj"
                                         :is-disabled="isDisabled"
                                         :date-format="dateFormat">
                </datepicker-range-single>
                <div>
                    <span>
                        <!--<button type="button" class="btn btn-info btn-sm" @click="onToday()">{{todayText}}</button>-->
                        <button type="button" class="btn btn-danger btn-sm" @click="onClean()">{{cleanText}}</button>
                    </span>
                    <span class="ms-span-right">
                        <button type="button" class="btn btn-danger btn-sm" @click="onClose()">{{closeText}}</button>
                    </span>
                </div>
            </div>
        </div>
        <div class="input-group">
            <input v-el:date-field type="text" class="form-control" v-model="dateRange" placeholder="{{placeholder}}" @click="showCalendarInput($event)" >
            <div class="input-group-addon ms-calendar" @click="showCalendar($event)">
                <span class="glyphicon glyphicon-calendar" ></span>
            </div>
        </div>
    </div>
</template>
<script>
    import Vue from "vue";
    import MSUtil from "../../util/index";
    import moment from "moment";
    import dateFieldMixin from "./mixin/dateFieldMixin";
    import datepickerRangeSingle from "../../picker/date/datepicker-range-single.vue";
    export default{
        name:'dateRangeSingleField',
        mixins:[dateFieldMixin],
        replace:false,
        props:{
            "startDay":{
                type:String,
                twoWay:true
            },
            "endDay":{
                type:String,
                twoWay:true
            },
            "selectedDates":{
                type:Array,
                twoWay:true,
                default:function () {
                    return [];
                }
            },
            "dateFormat":{
                type:String,
                default:function () {
                    return "YYYY-MM-DD";
                }
            },
            "isDisabled":{
                type:Function
            },
            "isFocus":{
                type:Boolean,
                default:function(){
                    return false;
                }
            }
        },
        data(){
            return {
                "calendarShow":false,
                "startDayObj":null,
                "endDayObj":null,
                "selectedDatesObj":[],
            }
        },
        ready(){
            let me = this;

            me.eventNamespace = _.uniqueId(".date_field_click_");
            $(document).on('click'+me.eventNamespace,function (e) {
                let el = me.$el;
                if(!el.contains(e.target)){
                    me.calendarShow = false;
                }
            });
        },
        computed:{
            "dateRange":function(){
                let me = this;
                if(me.startDay && me.endDay){
                    return me.startDay+"~"+me.endDay;
                }else {
                    return "";
                }
            }
        },
        watch:{
            "startDay":{
                handler:function (newValue,oldValue) {
                    let me = this;
                    if(newValue && newValue!=oldValue){
                        me.startDayObj = new Date(MSUtil.MSDate.dateAdapter(newValue));
                    }
                },
                //immediate:true
            },
            "endDay":{
                handler:function (newValue,oldValue) {
                    let me = this;
                    if(newValue && newValue!=oldValue){
                        me.endDayObj = new Date(MSUtil.MSDate.dateAdapter(newValue));
                    }
                },
                //immediate:true
            },
            "startDayObj":{
                handler:function (newValue,oldValue) {
                    let me = this;
                    if(newValue && JSON.stringify(newValue)!=JSON.stringify(oldValue)){
                        me.startDay = moment(newValue).format(me.dateFormat);
                    }
                },
                //immediate:true
            },
            "endDayObj":{
                handler:function (newValue,oldValue) {
                    let me = this;
                    if(newValue && JSON.stringify(newValue)!=JSON.stringify(oldValue)){
                        me.endDay = moment(newValue).format(me.dateFormat);
                    }
                },
                //immediate:true
            },
            "selectedDatesObj":{
                handler:function (newValue,oldValue) {
                    let me = this;
                    if(newValue && JSON.stringify(newValue)!=JSON.stringify(oldValue)){
                        let dates = [];
                        _.forEach(newValue,function (date) {
                            dates.push(moment(date).format(me.dateFormat));
                        });
                        me.selectedDates = dates;
                    }
                }
            },
            "selectedDates":{
                handler:function (newValue,oldValue) {
                    let me = this;
                    if(newValue && JSON.stringify(newValue)!=JSON.stringify(oldValue)){
                        let dates = [];
                        _.forEach(newValue,function (date) {
                            dates.push(new Date(MSUtil.MSDate.dateAdapter(date)));
                        });
                        me.selectedDatesObj = dates;
                    }
                },
                immediate:true
            }
        },
        methods:{
            "showCalendar":function (e) {
                let me = this;
                me.calendarShow = !me.calendarShow;
                me.$els.dateField.focus();
                if(e.clientY < (document.documentElement.clientHeight/2)){
                    Vue.util.addClass(me.$els.datePicker,"ms-picker-box-down");
                    Vue.util.removeClass(me.$els.datePicker,"ms-picker-box-up");
                }else {
                    Vue.util.addClass(me.$els.datePicker,"ms-picker-box-up");
                    Vue.util.removeClass(me.$els.datePicker,"ms-picker-box-down");
                }
            },
            "showCalendarInput":function(e){
                if(this.isFocus){
                    this.showCalendar(e);
                }                
            },
            "onClose":function () {
                let me = this;
                me.calendarShow = false;
            },
            "onClean":function () {
                let me = this;
                me.startDayObj = null;
                me.endDayObj = null;
                me.startDay = "";
                me.endDay = "";
                me.selectedDatesObj = [];
                me.selectedDates = [];
            },
            "onToday":function () {
                let me = this;
                me.startDayObj = new Date();
                me.endDayObj = new Date();
            }
        },
        components:{
            datepickerRangeSingle
        }
    }
</script>
<style lang="less" scoped>
.ms-date-range-single-field{
    .ms-calendar{
        cursor: pointer;
    }
    .ms-picker{
        position:relative;
        .ms-picker-box{
            position: absolute;
            display: inline-block;
            padding: 9px;
            background-color: #f5f5f5;
            border: 1px solid #e3e3e3;
            border-radius: 4px;
            -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.05);
            box-shadow: inset 0 1px 1px rgba(0,0,0,.05);
            z-index: 1000;

            .ms-span-right{
                float: right;
            }
        }
        .ms-picker-box-up{
            bottom: 0px;
        }
        .ms-picker-box-down{
            top:34px;
        }
    }
}
</style>



